import {useDispatch, useSelector} from "react-redux";
import {
    Avatar,
    Button,
    CircularProgress,
    Container,
    Grid, List, ListItem, ListItemAvatar, ListItemText, ListSubheader,
    Paper,
    Typography
} from "@mui/material";
import Web3 from "web3";
import coineus_abi from '../abis/coineus.json';
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {updateWalletBalance, updateWalletCoineusInfo, updateWalletFUSDBalance} from "../redux/slices/coineusSlice";

import cns_logo from "../images/logo192.png";
import fuse_logo from "../images/fuse_logo.png";
import fuse_logo_200 from "../images/fuse_logo200x200.png";
import fusd_logo from "../images/fusd_logo.png";
import getPairPrices from "../utils/fuse_price";

export default function HomePage() {

    const dispatch = useDispatch();

    const {wallet, prices} = useSelector(state => state.coineus);

    const CoineusContractAddress = '0xc2E299b47398963c618De5b05C6BdeCd4cc64022';

    const [pending, setPending] = useState(false);

    const [totalD, setTotalD] = useState(0);

    useEffect(() => {
        if (wallet.address) {
            getUserInfo();
        }
    }, [wallet.address]);

    //updates coineus info
    useEffect(() => {
        getCoineusInfo();
        const interval = setInterval(getCoineusInfo, 60000)
        return () => clearInterval(interval);
    }, [])

    const getCoineusInfo = () => {
        const web3 = new Web3('https://rpc.fuse.io');
        const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);

        const calls = [
            contract.methods.getTotalDividendsDistributed().call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            let [
                td
            ] = resp;

            setTotalD(td / (10 ** 18));
        })

    }

    const getUserInfo = () => {
        const web3 = new Web3('https://rpc.fuse.io');


        web3.eth.getBalance(wallet.address).then(resp => {
            dispatch(updateWalletBalance(resp / (10 ** 18)))
        })

        const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);
        const fusd_contract = new web3.eth.Contract(coineus_abi, '0x249be57637d8b013ad64785404b24aebae9b098b');

        const calls = [
            contract.methods.balanceOf(wallet.address).call,
            contract.methods.getAccountDividendsInfo(wallet.address).call,
            fusd_contract.methods.balanceOf(wallet.address).call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            let [
                balance,
                info,
                fusd_balance
            ] = resp;

            let withdrawableDividends = info[3];
            let totalDividends = info[4];
            let secondsUntilAutoClaimAvailable = info[7];

            balance = balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 18);
            totalDividends = totalDividends / (10 ** 18);

            dispatch(updateWalletCoineusInfo({
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

            dispatch(updateWalletFUSDBalance(fusd_balance / (10 ** 18)));

        })
    }

    const claim = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);

            contract.methods
                .claim()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getUserInfo();
                    setPending(false);
                });
        }
    }

    return (<Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom:20}}>

                <Grid item xs={12}>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <Paper>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar src={fuse_logo_200}/>
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={`$${prices.fuse?.inUSD.toLocaleString()}`}
                                        primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>
                        <Grid item xs={6}>
                            <Paper>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar src={cns_logo}/>
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={`$${prices.cns?.inUSD.toLocaleString()}`}
                                        primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <Grid container spacing={2}>
                        <Grid item xs={4}>
                            <Button variant="contained" fullWidth href="https://app.voltage.finance/#/swap?inputCurrency=0xc2e299b47398963c618de5b05c6bdecd4cc64022" target="_blank">Swap</Button>
                        </Grid>
                        <Grid item xs={4}>
                            <Button variant="contained" fullWidth href="https://www.dextools.io/app/fuse/pair-explorer/0x0f2e60b799f2237633754f8eec7970c125e15d75" target="_blank">Charts</Button>
                        </Grid>
                        <Grid item xs={4}>
                            <Button variant="contained" fullWidth href="https://app.voltage.finance/#/bridge" target="_blank">Bridge</Button>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <Paper>
                        <ListItem>
                            <ListItemText
                                primary={`${totalD.toLocaleString('en-US', {
                                    maximumFractionDigits: 2,
                                    minimumFractionDigits: 2
                                })} fUSD` || '--'}
                                secondary="Total Rewards Distributed "
                                primaryTypographyProps={{textAlign: 'right', fontWeight: 700, fontSize: '2.125rem'}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{color: '#000', backgroundColor: '#fbca00'}}>
                        <List>
                            <ListSubheader style={{color: '#000', backgroundColor: '#fbca00'}}>
                                <Typography variant="h5" fontWeight={700}>Buys/Sells</Typography>
                            </ListSubheader>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar style={{color: '#fbca00', backgroundColor: '#000'}}
                                            sx={64}><Typography fontWeight={700}>5%</Typography></Avatar>
                                </ListItemAvatar>
                                <ListItemText primary="Rewards in fUSD"/>
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar style={{color: '#fbca00', backgroundColor: '#000'}}
                                            sx={64}><Typography fontWeight={700}>2%</Typography></Avatar>
                                </ListItemAvatar>
                                <ListItemText primary="Back to Liquidity"/>
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar style={{color: '#fbca00', backgroundColor: '#000'}}
                                            sx={64}><Typography fontWeight={700}>2%</Typography></Avatar>
                                </ListItemAvatar>
                                <ListItemText primary="Assurance Fund"/>
                            </ListItem>
                            <ListSubheader style={{color: '#000', backgroundColor: '#fbca00'}}>
                                <Typography variant="h5" fontWeight={700}>Transfers</Typography>
                            </ListSubheader>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar style={{color: '#fbca00', backgroundColor: '#000'}}
                                            sx={64}><Typography fontWeight={700}>0%</Typography></Avatar>
                                </ListItemAvatar>
                                <ListItemText primary="Wallet to Wallet"/>
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <Typography
                                    variant="h4">{wallet.coineusInfo?.totalDividends.toLocaleString() || '--'} fUSD</Typography>
                                <Typography variant="caption">Total Dividends All-time</Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <Typography
                                    variant="h4">{wallet.coineusInfo?.withdrawableDividends.toLocaleString() || '--'} fUSD</Typography>
                                <Typography variant="caption">Claimable Dividends</Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <Button onClick={claim} variant='contained' fullWidth
                                        disabled={!wallet.address || wallet.coineusInfo?.secondsUntilAutoClaimAvailable > 0}>Claim</Button>
                            </Grid>
                            {
                                wallet.coineusInfo?.secondsUntilAutoClaimAvailable > 0 && <Grid item xs={12}>
                                    <Typography>{wallet.coineusInfo?.secondsUntilAutoClaimAvailable} Seconds until claim
                                        available</Typography>
                                </Grid>
                            }
                        </Grid>
                    </Paper>
                </Grid>

                {
                    pending && <Grid item xs={12}><CircularProgress size={20}/><Typography variant='caption' style={{
                        verticalAlign: 'top',
                        marginLeft: 10
                    }}>Transaction Pending</Typography></Grid>
                }

                <Grid item xs={12}>
                    <List>
                        <Paper>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar src={fuse_logo}/>
                                </ListItemAvatar>
                                <ListItemText
                                    primary='Fuse'
                                    secondary={`$${prices.fuse?.inUSD.toLocaleString() || '--'}`}
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                                <ListItemText
                                    primary={wallet.balance?.toLocaleString() || '--'}
                                    secondary={`$${wallet.balance ? (prices.fuse?.inUSD * wallet.balance).toLocaleString() : '--'}`}
                                    primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    secondaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        </Paper>
                    </List>

                    <List>
                        <Paper>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar src={cns_logo}/>
                                </ListItemAvatar>
                                <ListItemText
                                    primary='CNS'
                                    secondary={`$${prices.cns?.inUSD.toLocaleString() || '--'}`}
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                                <ListItemText
                                    primary={wallet.coineusInfo?.balance.toLocaleString() || '--'}
                                    secondary={`$${wallet.coineusInfo?.balance ? (prices.cns?.inUSD * wallet.coineusInfo?.balance).toLocaleString() : '--'}`}
                                    primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    secondaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        </Paper>
                    </List>

                    <List>
                        <Paper>
                            <ListItem>
                                <ListItemAvatar>
                                    <Avatar src={fusd_logo}/>
                                </ListItemAvatar>
                                <ListItemText
                                    primary='fUSD'
                                    secondary="$1.00"
                                    primaryTypographyProps={{fontWeight: 700}}
                                />
                                <ListItemText
                                    primary={wallet.fusd_balance?.toLocaleString() || '--'}
                                    secondary={`$${wallet.fusd_balance ? (1 * wallet.fusd_balance).toLocaleString() : '--'}`}
                                    primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    secondaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        </Paper>
                    </List>
                </Grid>
            </Grid>
        </Container>
    );
}