//fUSD 0x249be57637d8b013ad64785404b24aebae9b098b
//fUSD/FUSE pair 0x933a10d094592eb3f2a26bcb366472eba8868a66
import store from "../redux/store";
import Web3 from "web3";
import unipair_abi from "../abis/IUniswapV2Pair.json";
import {makeBatchRequest} from "./promisify";
import {updatePrices} from "../redux/slices/coineusSlice";

export default function getPairPrices() {

    const _store = store.getState();

    const {prices} = _store.coineus;

    const web3 = new Web3('https://rpc.fuse.io/');

    const fusd_fuse = new web3.eth.Contract(unipair_abi, prices.fUSDFUSEpair);
    const cns_fuse = new web3.eth.Contract(unipair_abi, prices.CNSFUSEpair);

    const calls = [
        fusd_fuse.methods.getReserves().call,
        cns_fuse.methods.getReserves().call
    ]

    makeBatchRequest(web3, calls)
        .then(resp => {
            const [
                fusd_fuse_reserves,
                cns_fuse_reserves
            ] = resp

            store.dispatch(updatePrices({
                name: 'fuse',
                price: {
                    inUSD: fusd_fuse_reserves.reserve1/fusd_fuse_reserves.reserve0,
                    inFuse: 1
                }
            }));

            store.dispatch(updatePrices({
                name: 'fusd',
                price: {
                    inUSD: 1,
                    inFuse: fusd_fuse_reserves.reserve0/fusd_fuse_reserves.reserve1
                }
            }));

            store.dispatch(updatePrices({
                name: 'cns',
                price: {
                    inFuse: cns_fuse_reserves.reserve0/cns_fuse_reserves.reserve1,
                    inUSD: cns_fuse_reserves.reserve0/cns_fuse_reserves.reserve1 * fusd_fuse_reserves.reserve1/fusd_fuse_reserves.reserve0
                }
            }));
        });
}